import InfiniteScroll from 'infinite-scroll'; // Importuje modu do obsugi nieskoczonego przewijania.
import Notiflix from 'notiflix';
import { fetchQuery } from './fetch';
import { loadMovies } from './loadMovies';

const moviesContainer = document.querySelector('.films__list');
let isLoading = false; // Flaga informujca o tym, czy trwa adowanie danych.
let pageNumber = 1;

async function fetchSearchResults() {
  console.log('Fetching...'); // Info czy pobieraj si filmy
  if (isLoading) return; // Sprawdza, czy trwa ju adowanie innych danych.

  isLoading = true; // Ustawia flag na true, oznaczajc rozpoczcie adowania.

  try {
    const response = await fetchQuery(pageNumber); // Pobiera wyniki wyszukiwania za pomoc funkcji fetchQuery.
    const movies = response && response.results; // Sprawdza, czy s wyniki wyszukiwania.

    if (movies) {
      loadMovies(movies); // aduje filmy do kontenera, jeli s dostpne.
      pageNumber++;
      if (movies.length < 20) {
        Notiflix.Notify.info('No more movies to load.'); // Wywietla komunikat o braku kolejnych filmw. Co tu nie dziaa, console.log te nie apie.
      }
    } else {
      Notiflix.Notify.info('No more movies to load.'); // Wywietla komunikat o braku kolejnych filmw. Co tu nie dziaa, console.log te nie apie.
    }
  } catch (error) {
    console.log('Error: ' + error);
  }

  isLoading = false; // Ustawia flag na false, sygnalizujc zakoczenie adowania.
}

let infScroll = new InfiniteScroll(moviesContainer, {
  path() {
    return `search/movie?query=&page=${pageNumber}`; // Ustawia ciek do pobrania kolejnych wynikw w nieskoczonym przewijaniu.
  },
  append: '.films__list-item', // Okrela elementy do dodania podczas adowania kolejnych wynikw.
});

infScroll.on('load', function () {
  console.log('InfScrolling'); // Info czy inf scroll dziaa.
  fetchSearchResults(); // Wywouje funkcj pobierania kolejnych wynikw.
});

function handleSearchInputChange() {
  pageNumber = 1; // Resetuje numer strony na 1.
  moviesContainer.innerHTML = ''; // Czyci kontener filmw.

  fetchSearchResults(); // Wywouje funkcj pobierania wynikw dla nowego zapytania.
}

const searchInput = document.querySelector('.header__nav-input'); // Pobiera pole wprowadzania zapytania.
searchInput.addEventListener('submit', handleSearchInputChange); // Dodaje nasuchiwanie na zmiany w polu wprowadzania zapytania.```
